<template>
  <main class="applog__inquiry__main">
    <section class="box__shadow__01 vertical__scroll grid__wrap">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["앱 로그 조회"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["앱 로그 조회"].subTitle }}
      </h5>
      <div class="search__wrap">
        <ul>
          <li>
            <select></select>
          </li>
          <li class="calendar__wrap top">
            <input type="text" readonly :value="startDate" />
            <span class="calendar__icon" @click="clickCalendar('startDate')"
              ><img
                src="@/assets/images/icon/ic-calendar.svg"
                alt="달력 아이콘"
            /></span>
            <VDatePicker
              @input="clickCalendarDate($event, 'startDate')"
              locale="ko"
              color="gray"
              :value="startDate"
              :class="{ active: calendarShow1 }"
            />
          </li>
          <li class="wave f__regular">~</li>
          <li class="calendar__wrap top">
            <input type="text" readonly :value="endDate" />
            <span class="calendar__icon" @click="clickCalendar('endDate')"
              ><img
                src="@/assets/images/icon/ic-calendar.svg"
                alt="달력 아이콘"
            /></span>
            <VDatePicker
              @input="clickCalendarDate($event, 'endDate')"
              locale="ko"
              color="gray"
              :value="endDate"
              :class="{ active: calendarShow2 }"
            />
          </li>
          <li>
            <button class="custom__btn">
              <span>
                <img
                  src="@/assets/images/icon/ic-search.svg"
                  alt="검색 아이콘"
                />
              </span>
              <p>검색</p>
            </button>
          </li>
        </ul>
      </div>
      <div class="grid__wrap">
        <Grid class="on" ref="gridEl" :pagination="true" :sort="true"></Grid>
      </div>
    </section>
  </main>
</template>
<script>
import "@/assets/css/calendar.css";
import "@/assets/css/appLogInquiry.css";
import VDatePicker from "v-calendar/lib/components/date-picker.umd";
import dateParse from "@/utill/dateParse.js";
import { Grid } from "gridjs-vue";
export default {
  data() {
    return {
      calendarShow1: false,
      calendarShow2: false,
      startDate: "",
      endDate: "",
      cols: [
        { name: "번호", id: "index" },
        { name: "진료과목", id: "subject" },
        { name: "메뉴", id: "menu" },
        { name: "이름", id: "name" },
        { name: "액션", id: "action" },
        { name: "데이터", id: "date" },
        { name: "사용자명", id: "userNm" },
        { name: "변경일시", id: "updateTime" },
      ],
    };
  },
  components: {
    VDatePicker,
    Grid,
  },
  methods: {
    clickCalendar(evt) {
      if (evt === "startDate") {
        this.calendarShow2 = false;
        this.calendarShow1 === false
          ? (this.calendarShow1 = true)
          : (this.calendarShow1 = false);
      } else if (evt === "endDate") {
        this.calendarShow1 = false;
        this.calendarShow2 === false
          ? (this.calendarShow2 = true)
          : (this.calendarShow2 = false);
      }
    },
    clickCalendarDate(param, inputName) {
      if (inputName === "startDate") {
        this.startDate = dateParse.dateToString(param);
        if (this.$env == "development") console.log(this.startDate);
        this.calendarShow1 = false;
      } else if (inputName === "endDate") {
        this.endDate = dateParse.dateToString(param);
        this.calendarShow2 = false;
      }
    },
  },
};
</script>
